    margin-top: 30px;
    margin-bottom: 30px;
    border-top: 1px solid rgba(165, 165, 165, 0.1);
    padding-top: 20px;
   
}
.item-content-detail h1{
    overflow: hidden;  
}
.item-content-detail p{
    font-size: 16px;
    line-height: 26px;
    color: #FFFFFF;
    width: 500px;
}
.item-content-buy .primary-btn{
    background: var(--primary-btn);
    border-radius: 10px;
    border: none;
    padding: 0.75rem 3rem;
    font-size: 14px;
    line-height: 21px;
    font-weight: 600;
    color: #fff;
    margin-right: 20px;
}
.item-content-buy .secondary-btn{
    background: transparent;
    border-radius: 10px;
    border: 1px solid var(--primary-color);
    padding: 0.70rem 3rem;
    font-size: 14px;
    line-height: 21px;
    font-weight: 600;
    color: var(--primary-color);
}
.item-content-action{
    display: flex;
    position: absolute;
    right: 0;
}
.item-content-action p{
    cursor: pointer;
    font-size: 18px;
    color: white;
    background: var(--primary-btn);
    margin-inline: 10px;
    border-radius: 5px;
    padding: 5px 5px 0px 5px;
}
@media screen and (max-width: 1050px) {
    .item{
        flex-direction: column;
    }
    .item-content{
        margin: 0 0 3rem;
    }
    .item-image{
        border-right: unset;
    }
    .item-content-detail p{
        width: 100%;
    }

}

@media screen and (max-width: 650px) {
    .item-content h1{
        font-size: 28px;
        line-height: 42px;
    }
    .item-content p{
        font-size: 16px;
        line-height: 24px;
    }
    .item-content__people{
        flex-direction: column;
    }
    .item-content__people p{
        margin: 0;
    }
    .item-content__input input,
    .item-content__input button{
        font-size: 16px;
        line-height: 24px;
    }
    .item-content-buy .primary-btn{
        padding: 0.75rem 20px;

    }
    .item-content-buy .secondary-btn{
        padding: 0.75rem 33px;
    }
}
@media screen and (max-width: 490px){
    .item-content h1{
        font-size: 28px;
        line-height: 42px;
    }
    .item-content p{
        font-size: 14px;
        line-height: 26px;
    }
    .item-content__input input,
    .item-content__input button{
        font-size: 12px;
        line-height: 16px;
    }
    .item{
        padding-inline:1rem !important ;
        padding-top: 1rem !important;
    }
    .item-image img{
        padding: 0;
        border-radius: 20px;
        margin-bottom: 25px;
    }
    .item-content-buy .primary-btn{
        margin-right: 10px;

    }
    .item-content-action{
        top: 15%;
    }
    .item-content-action p{
        margin-inline: 5px;

    }

}

:root {
  --font-family: 'Raleway', sans-serif;

  --gradient2: linear-gradient(89.97deg, #EB1484 1.84%, #c81cc5 102.67%);
  --primary-btn: linear-gradient(101.12deg, #EB1484 27.35%, #C91CC3 99.99%, #C81CC5 100%, #C81CC5 100%);

  --primary-color: #EB1484;
  --secondary-color: #c81cc5;

  --color-bg: linear-gradient(180deg, #13215c, #382764, #532e6b, #6d3571, #853d75, #9b4778, #b0537a, #c4607b, #d56e7c, #e57e7e, #f38f7f, #ffa182);
  --color-card : #2A2D3A;

}
/* ===== Scrollbar CSS ===== */
  /* Firefox */
  * {
    scrollbar-width: 2px;
    scrollbar-color: #2A2D3A #ffffff;
    overflow-x:hidden;
  }

  /* Chrome, Edge, and Safari */
  *::-webkit-scrollbar {
    width: 4px;
    
  }

  *::-webkit-scrollbar-track {
    background: transparent;
  }

  *::-webkit-scrollbar-thumb {
    background: #ff0c20;
    border-radius: 10px;
    border: 3px none #ffffff;
  }

